.login{
    width: 500px;
    margin: 100px auto;
    height: 280px;
    background-color: darkseagreen;
    padding: 50px;
    box-sizing: border-box;
    text-align: center;

}
.login h1{
    text-align: center;
}

.login .firstinput{
    width: 400px;
    height: 50px;
    margin-bottom: 20px;

}
.login input{
    width: 400px;
    height: 50px;
    margin-bottom: 20px;
    box-sizing: border-box;
}

.login button{
    width: 150px;
    height: 50px;
    border: none;
    padding: 0;
}

.login button:last-child{
    margin-left: 100px;
}

.hidden{
    display: none;
}

.show{
    width: 100px;
    height: 100px;
    display: block;
    position: relative;
}

